<template>
  <div class="back">
    <app-bar
      back-icon
      title="新闻中心"
      hideMessage
      hideDemand
      hideUserMenu
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)">
      <v-container>
        <v-card class="mx-auto mt-12" min-height="400" max-width="1000">
          <p class="text-center pt-8 headline">{{ title }}</p>
          <div class="dotted_line mt-3 mx-5"></div>
          <p class="text-center grey--text text--darken-2 body-2 mt-5">
            <span class="mr-3">发布者: {{ publisher }}</span>
            <span class="mx-3">发布时间：{{ time }}</span>
            <span class="ml-3">点击量: {{ click }}</span>
          </p>
          <div class="mx-8">
            <p v-html="context">{{ context }}</p>
          </div>
          <div class="dotted_line mt-3 mx-5"></div>
          <div class="ml-8 mt-2">
            <p>
              <span
                >上一条：<a @click="lastAndNext(last.id)" v-if="last">{{
                  last.title
                }}</a></span
              >
            </p>
            <p>
              <span
                >下一条：<a @click="lastAndNext(next.id)" v-if="next">{{
                  next.title
                }}</a></span
              >
            </p>
          </div>
        </v-card>
      </v-container>
    </v-content>
    <AppFooter></AppFooter>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'New',
  props: ['id'],
  components: {
    AppBar,
    AppFooter
  },
  data() {
    return {
      title: '',
      publisher: '',
      time: '',
      context: '',
      click: '',
      fileURL: '',
      last: {},
      next: {}
    }
  },
  watch: {
    $route() {
      this.getNew()
    }
  },
  methods: {
    async getNew() {
      let res = await this.$http.get('/api/home/new?id=' + this.id)
      if (res.data.code === 200) {
        this.title = res.data.data.title
        this.publisher = res.data.data.publisher
        this.time = res.data.data.time
        this.context = res.data.data.context
        this.click = res.data.data.click
        this.fileURL = res.data.data.fileURL
      }
      let lastRes = await this.$http.get('/api/home/newlast?id=' + this.id)
      if (lastRes.data.code === 200) {
        this.last = lastRes.data.data
      }
      let nextRes = await this.$http.get('/api/home/newnext?id=' + this.id)
      if (nextRes.data.code === 200) {
        this.next = nextRes.data.data
      }
    },
    lastAndNext(id) {
      this.$router.push('/newsList/detail/' + id)
      this.$router.go(0)
    }
  },
  mounted() {
    this.getNew()
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
.dotted_line {
  width: 95%;
  height: 0;
  border-bottom: #c6c6c6 1px dashed;
}
</style>
